 <template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
           $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">新增</span>
    </mu-appbar> -->
    <van-nav-bar
      title="新增"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="filter-o" slot="right" size="20" /> -->
    </van-nav-bar>
    <div class="body">
      <div class="boxFlex styleClass">
        <div style="flex:2">
          <span>地区：</span>
        </div>
        <div style="flex:5" @click="selectCity">
          <input type="text" class="input" placeholder="选择省/市/区" disabled v-model="area" />
        </div>
      </div>
      <div class="boxFlex styleClass">
        <div style="flex:2">
          <span>详细地址：</span>
        </div>
        <div style="flex:5">
          <input type="text" class="input" placeholder="请填写详细地址" v-model="address" />
        </div>
      </div>
      <div class="bottomSubmit">
        <div class="btnSubmit backgroundNaviBar" @click="submit()">
          <span>保存地址</span>
        </div>
      </div>
    </div>
    <van-popup v-model="show" position="bottom">
      <van-area
        :area-list="areaList"
        :columns-placeholder="['请选择', '请选择', '请选择']"
        title="选择地址"
        @confirm="confirmCity"
      />
    </van-popup>
  </div>
</template>
<script>
import City from "./aseets";
import Vue from "vue";
import { Area, Toast } from "vant";
import AppBace from "../../../static/ajax";
// import { Toast } from "vant";

Vue.use(Toast);
Vue.use(Area);
export default {
  data() {
    return {
      areaList: City,
      show: false,
      area: "",
      address: ""
    };
  },
  created() {
    this.token = sessionStorage.getItem("token");
  },
  methods: {
    selectCity() {
      this.show = true;
    },
    confirmCity(val) {
      this.show = false;
      var name = "";
      var nameArr = [];
      console.log(val);
      for (let index = 0; index < val.length; index++) {
        if (val[index] !== undefined && val[index].name !== "") {
          name = val[index].name;
        } else {
          name = "";
        }
        nameArr.push(name);
      }
      console.log(nameArr[0] + nameArr[1] + nameArr[2]);
      this.area = nameArr[0] + nameArr[1] + nameArr[2];
    },
    async submit() {
      var url = "/wastetransfer/wastetransfer/destination";
      var desList = [];
      var address = this.area + this.address;
      desList.push(address);
      var res = await AppBace.request(url, "POST", desList, this.token);
      console.log(res);
      if (res.data.message === "success") {
        Toast.success("地址保存成功");
        this.area = "";
        this.address = "";
        address = "";
        desList = [];
      } else {
        Toast.fail("地址保存失败");
      }
    }
  }
};
</script>
<style  scoped>
.input {
  border: none;
  background-color: white;
}
.boxFlex {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.styleClass {
  height: 50px;
  background-color: white;
  padding: 10px;
  border-bottom: 1px #e9e9e9 solid;
}
.bottomSubmit {
  display: flex;
  justify-content: center;
}
.btnSubmit {
  height: 40px;
  color: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 96%;
  margin-top: 10px;
}
</style>


